import React from 'react';
import videojs from 'video.js'
import { Icon, Row, Col } from 'antd';

import 'video.js/dist/video-js.css'

export default class VideoPlayer extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            playing: false,
            liked: false
        };
        this.onClickPlay = this.onClickPlay.bind(this);
        this.onClickLike = this.onClickLike.bind(this);
    }

    componentDidMount() {
        // instantiate Video.js
        this.player = videojs(this.videoNode, this.props, function onPlayerReady() {
            console.log('onPlayerReady', this)
        });
    }

    // destroy player on unmount
    componentWillUnmount() {
        if (this.player) {
            this.player.dispose()
        }
    }

    onClickPlay() {
        if(this.state.playing) {
            this.player.pause()
        } else {
            this.player.play()
        }

        this.setState({
            playing: !this.state.playing
        })
    }

    onClickLike(){
        this.setState({
            liked: !this.state.liked
        })
    }

    // wrap the player in a div with a `data-vjs-player` attribute
    // so videojs won't create additional wrapper in the DOM
    // see https://github.com/videojs/video.js/pull/3856
    render() {
        return (
            <div>
                <div data-vjs-player className='video-content'>
                    <video ref={node => this.videoNode = node} className="video-js"/>
                </div>
                <Row className='description'>
                    <Col span={4} >
                        <Icon type={this.state.playing ? 'pause-circle-o': 'play-circle-o'} style={{fontSize: 20, float: 'left', margin: 10}} onClick={this.onClickPlay}/>
                    </Col>
                    <Col span={2} offset={18}>
                        <Icon type={this.state.liked ? 'like' :'like-o'} style={{fontSize: 20, float: 'right', margin: 10}} onClick={this.onClickLike}/>
                    </Col>
                </Row>
            </div>
        )
    }
}


